adas de aves.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema7-4-tabs-imagen6.jpg"),"alt":"Partículas respirables"}})])])])])]),_vm._m(24),_c('div',{staticClass:"row justify-content-center mb-5",attrs:{"data-aos":"fade"}},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"tarjeta color-primario py-lg-4 px-lg-5 p-3"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_vm._m(25),_c('div',{staticClass:"col"},[_c('div',{staticClass:"row justify-content-between align-items-center"},[_vm._m(26),_c('div',{staticClass:"col-sm-auto"},[_c('a',{staticClass:"boton color-acento-botones texto-negro",attrs:{"href":('https://www.google.com/url?q=https://fenavi.org/wp-content/uploads/2018/05/GUIA_AMBIENTAL_SUBSECTOR_AVICOLA.pdf&sa=D&source=editors&ust=1626817371108000&usg=AOvVaw1gj6GcHU_FZquzXbtwKctN'),"target":"_blank"}},[_c('span',[_vm._v("Descargar")]),_c('i',{staticClass:"fas fa-file-download"})])])])])])])])]),_c('h3',{staticClass:"mb-4",attrs:{"data-aos":"fade-right"}},[_vm._v("Olores ofensivos")]),_vm._m(27),_vm._m(28),_c('h3',{staticClass:"mb-4",attrs:{"data-aos":"fade-right"}},[_vm._v("Control de olores ofensivos")]),_vm._m(29),_c('h3',{staticClass:"mb-4",attrs:{"data-aos":"fade-right"}},[_vm._v("Autodiagnóstico de emisión de olores ofensivos ")]),_c('p',{attrs:{"data-aos":"fade"}},[_vm._v("El autodiagnóstico se practica en lugares aledaños como casas vecinas o puntos ubicados alrededor de la producción, los cuales han sido escogidos previamente. La recomendación es que se realice con regularidad sobre todo cuando el viento se dirige en dirección a los puntos seleccionados, con el fin de que se obtenga una mejor concentración de olores ofensivos provenientes de la producción. Esta actividad puede medir el impacto de actividades como la remoción de camas o el paleo de pilas de compost, las cuales pueden causar altos picos de emisión de olores.")]),_c('p',{staticClass:"mb-4",attrs:{"data-